import React, {Component} from 'react';
import ParallaxSection from './parallax';


class About extends Component {
  constructor() {
    super();
  }

  componentDidMount() {
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <div>
        <ParallaxSection backgroundImage="woody" centerText="CenterBoxText white" words="."/>
        <ParallaxSection backgroundImage="nomad" centerText="CenterBoxText gray fillBlack" words="."/>
        <ParallaxSection backgroundImage="books" centerText="CenterBoxText white" words="."/>
        <ParallaxSection backgroundImage="skybuilding" centerText="CenterBoxText gray" words="."/>
        <ParallaxSection backgroundImage="sippy" centerText="CenterBoxText gray" words=""/>
      </div>
    );
  }
}

export default About;


